import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  // Select,
  DatePicker,
  Row,
  Col,
  Divider,
  // Input
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/pettyCashSummaryReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";

import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
// const Option = Select.Option;
// const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

class PettyCashSummaryReport extends Component {
  state = {
    filter: {
      start_date: moment().startOf("month").format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_location: null
    },
  };

  // Get All data diawal
  componentDidMount() {
    const { getPettyCashSummaryReports } = this.props;
    getPettyCashSummaryReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCashSummaryReport.error) {
      notification("error", this.props.pettyCashSummaryReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.pettyCashSummaryReport.success) {
      notification("success", this.props.pettyCashSummaryReport.success);
      this.props.clearSuccess();
    }
  }

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
          "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
          "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getPettyCashSummaryReports } = this.props;
    //const { current_page } = this.props.pettyCashSummaryReport;
    let filter = this.filterValue();

    getPettyCashSummaryReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPettyCashSummaryReports } = this.props;
    this.setState({
      filter: {
        start_date: moment().startOf("month").format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_location: null
      },
    });
    getPettyCashSummaryReports();
  };

  render() {
    // console.log(this.props);

    // Data setup
    const {
      petty_cash_summary_reports, locations, filter, filter_select
    } = this.props.pettyCashSummaryReport;
    //console.log(this.props.pettyCashSummaryReport.base_currency); //console.log(this.state);

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "location_code",
        rowKey: "location_code",
        width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 2,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              children: <strong style={{ marginLeft: "50%" }}>{row.key}</strong>,
              props: {
                colSpan: 2
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "20%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.key === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "openingBalance" }),
        dataIndex: "opening_amount",
        rowKey: "opening_amount",
        width: "20%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "changes" }),
        align: "center",
        children: [
          {
            title: getLanguage({ id: "totalDebit" }),
            dataIndex: "debit_amount",
            rowKey: "debit_amount",
            width: "15%",
            align: "right",
            parse: true,
            render: (text, row) => {
              if (row.key === "Total") {
                return (
                  <strong>
                    <NumberFormat value={parseFloat(text)} />
                  </strong>
                );
              }

              return <NumberFormat value={parseFloat(text)} />;
            }
          },
          {
            title: getLanguage({ id: "totalCredit" }),
            dataIndex: "credit_amount",
            rowKey: "credit_amount",
            width: "15%",
            align: "right",
            parse: true,
            render: (text, row) => {
              if (row.key === "Total") {
                return (
                  <strong>
                    <NumberFormat value={parseFloat(text)} />
                  </strong>
                );
              }

              return <NumberFormat value={parseFloat(text)} />;
            }
          }
        ]
      },
      {
        title: getLanguage({ id: "endingBalance" }),
        dataIndex: "ending_amount",
        rowKey: "ending_amount",
        width: "20%",
        align: "right",
        parse: true,
        render: (text, row) => {
          if (row.key === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    ];

    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let total_beginning = 0;
    let total_debit = 0;
    let total_credit = 0;
    let total_ending = 0;
    petty_cash_summary_reports.map((data, key) => {
      total_beginning += parseFloat(data.opening_amount);
      total_debit += parseFloat(data.debit_amount);
      total_credit += parseFloat(data.credit_amount);
      total_ending += parseFloat(data.ending_amount);

      dataSource.push({
        ...data,
        key
      });
    });

    dataSource.push({
      key: "Total",
      location_code: "Total",
      opening_amount: total_beginning,
      debit_amount: total_debit,
      credit_amount: total_credit,
      ending_amount: total_ending
    });

    const { loading } = this.props.pettyCashSummaryReport;

    const fileName = getLanguage({ id: "pettyCashSummaryReports.pageHeader" })
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="pettyCashSummaryReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="pettyCashSummaryReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={
                () => (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                  "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loading}
              locations={locations}
              margin="1cm"
              scale={0.5}
              title="pettyCashSummaryReports.pageHeader"
            />

            <ExcelWrapper
              title="pettyCashSummaryReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button style={{ marginLeft: "5px" }} onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="purchaseInvoices.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                          this.state.filter.end_date
                          ? [
                            moment(this.state.filter.start_date),
                            moment(this.state.filter.end_date)
                          ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col
                    span={12}
                    style={{
                      marginTop: "20px"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter
              ? (<Empty description={<IntlMessages id="clickFilter" />} />)
              : petty_cash_summary_reports.length === 0 &&
                !this.props.pettyCashSummaryReport.loading ? (
                  <Empty
                    description={
                      <IntlMessages id="pettyCashSummaryReports.table.noData" />
                    }
                  />
                ) : (
                  <div className="isoInvoiceTable">
                    <Divider orientation="left" style={{ fontSize: 14 }}>
                      <IntlMessages id="details" />
                    </Divider>
                    <Scrollbars style={{ width: "100%" }}>
                      <TableWrapper
                        dataSource={dataSource}
                        columns={columns}
                        loading={loading}
                        className="invoiceListTable"
                        bordered={true}
                        // Set pagination, jika ada page_size mengunakan pagination API
                        // jika tidak ada page_size menggunakan pagination bawaan
                        pagination={false}
                      />
                    </Scrollbars>
                  </div>
                )
            }
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    pettyCashSummaryReport: state.pettyCashSummaryReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(PettyCashSummaryReport);
